.single_details_container_main{
    width: 100%;
   height: 100%;
   display: flex;
   align-items: center;
   justify-content: center;

    position: relative;
}

.single_details_container{
    width: 80%;
    display: flex;
    align-items: center;
    justify-content: center;
    padding-top: 100px;
    flex-direction: column;
    background: rgba(255, 255, 255, .2);
    backdrop-filter: blur(3px);
    position: relative;
    margin: 10px 0;
    padding-bottom: 10px;
}





.single_details_container .single_movie_title{
    color: white;
    font-family: 'Poppins', sans-serif;
    font-weight: 800;
    font-size: 2.5rem;

}

@media (max-width:768px) {
 .single_details_container .single_movie_title{
        color: white;
        font-family: 'Poppins', sans-serif;
        font-weight: 800;
        font-size: 1.45rem;
    
    }

    .single_details_container{
      width: 90%;
    }

   
}


.single_details_container .single_details_year{
    color: rgb(170, 170, 170);
    font-family: 'Poppins', sans-serif;
}

.single_details_container .single_details_tagline{
    color: rgb(170, 170, 170);
    font-family: 'Poppins', sans-serif;
}

.single_details_container .single_details_genre {
    color: rgb(255, 255, 255);
    font-family: 'Poppins', sans-serif;
    display: flex;
    font-size: 1rem;
    margin: 10px 0;
    font-weight: 600;
    align-items: center;
   

}

.single_details_container .single_details_rating{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    margin: 20px 0;
}

.single_details_container .single_details_overview {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    width: 100%;
    margin: 20px 0;
}

.single_details_container .single_details_overview .overview_title{
    color: white;
    font-family: 'Poppins', sans-serif;
    font-weight: 800;
    font-size: 1.2rem;
}

.single_details_container .single_details_overview p{
    width: 50%;
    color: rgb(143, 142, 142);
}

@media (max-width : 768px) {
  .single_details_container .single_details_overview p{
    width: 80%;
    color: rgb(143, 142, 142);
}
}

.single_details_container_main::after {
    content: "";
    top: 0;
    left: 0;
    position: absolute;
    background: linear-gradient(rgb(255, 40, 40), #f0f);
    width: 100%;
    height: 100%;
    clip-path: circle(20% at right 70%);
    z-index: -4;
  }
  
  .single_details_container_main::before {
    content: "";
    top: 0;
    left: 0;
    position: absolute;
    background: linear-gradient(rgb(56, 232, 255), #f0f);
    width: 100%;
    height: 100%;
    clip-path: circle(20% at 10% 10%);
    z-index: -4;
  }

  .single_details_container .single_details_production{
    display: flex;
    align-items: center;
    justify-content: center;
   flex-wrap: wrap;
    width: 60%;
    margin: 20px 0;

  }

  .single_details_production_title{
    color: white;
    font-family: 'Poppins', sans-serif;
    font-weight: 800;
    font-size: 1.2rem;
  }


  .Production_item_container{
    width: 250px;
   margin: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
  }

  .Production_item_img{
    width: 50%;
  }

  .production_item_name{
   color: rgb(143, 142, 142);
    font-family: 'Poppins', sans-serif;
    font-weight: 600;
    font-size: 1rem;
    margin-top: 10px;
  }

  .single_details_link{
    width: 200px;
    height: 40px;
    background-color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    color: black;
    border-radius: 10px;
    font-family: 'Poppins', sans-serif;
    font-weight: 600;
    font-size: 1rem;
   
  }

  .single_details_link:hover{
    opacity: 0.7;
  }



