@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700;800&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');

.slider_container_main{
    background: black;
   position: relative;
    overflow: hidden;
}

.slider_container{
    height: 550px;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
    animation: slide 2s  linear ;
}

.poster{
    position: absolute;
    width: 160px;
    bottom: 0;
    z-index: 2;
    left: 50px;
    box-shadow: 10px 10px 15px rgba(0, 0, 0, 0.8);
     
    
}

@media (max-width : 768px) {
    .slider_container{
        height: 350px;
      
    }
    .poster{
        width: 100px;
        left: 20px;
    }
}

.slider_image{
    width: 100%;
    
 
}

