.blurr {
    background-color:rgb(0, 0, 0);
    height: 200px;
    width: 120%;
    filter: blur(18px);
    position: relative;
    top: -60px;
    z-index: 1;
   
}



.title{
    position: absolute;
    color: rgba(255, 255, 255, .2);
    z-index: 2;
    bottom: 15px;
    right: 0px;
    width: 70%;
        font-size: 2.2rem;
    font-family: 'Poppins', sans-serif;
    font-weight: 800;
    height: 100px;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;

}

@media (max-width: 768px) {
    .title{
        font-size: 1.2rem;
    }
}

.details_container{
    width: 100%;

    /* border: 1px solid red; */
}

.details_title{
    color: rgba(255, 255, 255, 1);
    font-size: 2rem;
    font-family: 'Poppins', sans-serif;
    font-weight: 800;
    margin: 10px 0;
}

.year{
    color: white;
    opacity: 0.7;
    font-family: 'Poppins', sans-serif;
}

.language{
    color: white;
    opacity: 0.7;
    font-family: 'Poppins', sans-serif;
}

.overview{
    color: white;
    color: white;
    opacity: 0.9;
    font-family: 'Roboto', sans-serif;
    margin: 10px 0;
    font-size: 0.9rem;
}

.ratings_container{
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
}

.fulldetails{
    width: 150px;
