@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');

.container_main{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 55px;
    margin-right: 20px;
  
}

.main_circle{
    width: 55px;
    height: 55px;
    border-radius: 50%;
    /* background-image: conic-gradient(red 0deg, red 270deg, white 270deg, white 360deg); */
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    

}

.child_circle{
    height: 45px;
    width: 45px;
    border-radius: 50%;
   
    background-color: black;
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
}

.rating{
    
    font-family: 'Roboto', sans-serif;
}

.text{
color: rgb(182, 182, 182);
}