.images_container{
    height: 500px;
    display: flex;
    align-items: center;
    justify-content: center;
    /* overflow: hidden; */
    position: relative;
    background-position: center;
}

.images_container .backdrop{
    width: 100%;
}

.images_container .poster_img{
    width: 20%;
    position: absolute;
    bottom: -70px;
    z-index: 1;
    -webkit-box-shadow: 7px 9px 50px 15px rgba(0,0,0,0.4);
-moz-box-shadow: 7px 9px 50px 15px rgba(0,0,0,0.4);
box-shadow: 7px 9px 50px 15px rgba(0,0,0,0.4);

}

.images_container .black_filter{
    position: absolute;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.356);
    z-index: 0;
}

@media (max-width: 1000px) {
    .images_container .poster_img{
        width: 20%;
        
    }
    
    .images_container{
        height: 300px;
       
    }
}

@media (max-width: 768px) {
    .images_container .poster_img{
        width: 33%;
        bottom: -50px;
    }
    
    .images_container{
        height: 250px;
       
    }
}


