@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700;800&display=swap');



.all_contents{
    height: fit-content;
    width: fit-content;
    display: flex;
    align-items: center;
   justify-content: center;

  
    display: flex;
    align-items: center;
    flex-wrap: wrap;


}

.poster_container{
    height:200px;
    margin: 20px  20px;
    display: flex;

    align-items: center;
    justify-content: center;
    overflow: hidden;
}

.poster_link{
    height: 50%;
       z-index: 2;
    width: 100%;
    position: absolute;

}

.add_list{
    position: absolute;
    color: white;
    width: 30px;
    height: 40px;
    background: rgba(255, 255, 255, 0.2);
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 1.2rem;
    top: 0;
    right: 0;
}

.add_list:hover{
    opacity: 0.7;
}

.poster_container:hover .up{
   
    animation: shake 1s ease-out infinite;
}

@keyframes shake {
    0%{
        bottom: 10px;
    }
    100%{
        bottom: 20px;
    }
}

.popular_poster_image{
     height: 100%;

 
 
}

.heading_container{
    background-color: black;
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    height: 60px;

}




.up{
    color: white;
    position: absolute;
    bottom: 10px;
    font-size: 1.4rem;
    z-index: 1;
}

.poster_details{
    position: absolute;
    width: 100%;
    height: 100%;
    background: rgba(255, 255, 255, 0.25);
 display: none;
   animation: details_slide 1s ease;
   backdrop-filter: blur(3px);
   text-align: center;
   
}

.details-title{
    font-size: 1.1rem;
    font-family: 'Poppins', sans-serif; 
    font-weight: 600;
   margin-top: 5px;
    color: rgb(0, 0, 0);
}

.details-year{
    color: rgb(0, 0, 0);
   
    
}

.details-genre{
    font-size: 0.6rem;
    font-weight: bold;
   
}

.details-language{
    font-weight: bold;
    font-size: 0.8rem;
}

.details-button{
    border: none;
    font-size: 0.8rem;
    margin-top: 5px;
    border-radius: 5px;
    font-weight: 600;
}


.details-average{
    font-size: 0.8rem;
}
@keyframes details_slide {
    0%{
        transform: translateY(100%);
    }

    100%{
      transform: translateY(0);
    }
}

.Loading_container{
    width: 100%;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
}

.button_container{
    width: 100%;
    display: flex;
    justify-content: space-around;
}