@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700;800&display=swap');

.popular_container_main{
    width: 100%;
    height: 250px;
    background-color: rgb(0, 0, 0);
    /* display: flex;
    align-items: center; */
    position: relative;
    overflow: hidden;
}

.leftbtn{
    color: white;
    width: 50px;
    height: 100px;
    /* border: 2px solid white; */
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 2rem;
    /* opacity: 0.7; */
    background-color: rgba(255, 255, 255, 0.1);
    border-radius: 5px;
    position: absolute;
    left: 0;
    bottom: 0;
    top: 0;
    margin: auto;
    z-index: 3;
    border-top: .5px solid rgba(255, 255, 255, 0.2);
    border-right: .5px solid rgba(255, 255, 255, 0.2);

}

.leftbtn:hover{
    opacity: 0.5;
}

.rightbtn{
    color: white;
    width: 50px;
    height: 100px;
    /* border: 2px solid white; */
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 2rem;
    /* opacity: 0.7; */
    background-color: rgba(255, 255, 255, 0.1);
    border-radius: 5px;
    position: absolute;
    right: 0;
    bottom: 0;
    top: 0;
    margin: auto;
    z-index: 3;
    border-top: .5px solid rgba(255, 255, 255, 0.2);
    border-left: .5px solid rgba(255, 255, 255, 0.2);
   
}

.rightbtn:hover{
    opacity: 0.5;
}

.contents{
    height: 100%;
    width: fit-content;
transition: 0.5s linear;
    position: absolute;
    display: flex;
    align-items: center;


}

.poster_container{
    height:200px;
    margin: 0  20px;
    display: flex;
    position: relative;
    align-items: center;
    justify-content: center;
    overflow: hidden;
}

.poster_link{
    height: 50%;
       z-index: 2;
    width: 100%;
    position: absolute;

}

.add_list{
    position: absolute;
    color: white;
    width: 30px;
    height: 40px;
    background: rgba(255, 255, 255, 0.2);
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 1.2rem;
    top: 0;
    right: 0;
}

.add_list:hover{
    opacity: 0.7;
}

.poster_container:hover .up{
   
    animation: shake 1s ease-out infinite;
}

@keyframes shake {
    0%{
        bottom: 10px;
    }
    100%{
        bottom: 20px;
    }
}

.popular_poster_image{
     height: 100%;

 
 
}

.heading_container{
    background-color: black;
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    height: 60px;

}

.heading{
    color: rgb(218, 218, 218);
    font-size: 1.8rem;
    font-family: 'Poppins', sans-serif;
    font-weight: 800;
    margin-left: 10px;

}

.all_popular_btn{
    margin-bottom: 5px;
    margin-right: 10px;
    font-family: 'Poppins', sans-serif; 
    font-weight: 700;
    background: rgba(255, 255, 255, 0.2);
    border-radius: 5px;
    color: white;
   

}

.up{
    color: white;
    position: absolute;
    bottom: 10px;
    font-size: 1.4rem;
    z-index: 1;
}

.poster_details{
    position: absolute;
    width: 100%;
    height: 100%;
    background: rgba(255, 255, 255, 0.25);
 display: none;
   animation: details_slide 1s ease;
   backdrop-filter: blur(3px);
   text-align: center;
   
}

.details-title{
    font-size: 1.1rem;
    font-family: 'Poppins', sans-serif; 
    font-weight: 600;
   margin-top: 5px;
    color: rgb(0, 0, 0);
}

.details-year{
    color: rgb(0, 0, 0);
   
    
}

.details-genre{
    font-size: 0.6rem;
    font-weight: bold;
   
}

.details-language{
    font-weight: bold;
    font-size: 0.8rem;
}

.details-button{
    border: none;
    font-size: 0.8rem;
    margin-top: 5px;
    border-radius: 5px;
    font-weight: 600;
}

.details_view_all{
    color: white;
    width: 150px;
    height: 80%;
    background: rgba(255, 255, 255, 0.3);
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
}
.details-average{
    font-size: 0.8rem;
}
@keyframes details_slide {
    0%{
        transform: translateY(100%);
    }

    100%{
      transform: translateY(0);
    }
}