
.genre_main_container{
    width: 100%;
  
    
}

.genre_heading{
    color: rgb(218, 218, 218);
    font-size: 1.8rem;
    font-family: 'Poppins', sans-serif;
    font-weight: 800;
    margin-left: 10px;
}
.genre_container{
    width: 100%;
    display: flex;
    
    padding-top: 20px;
    padding-bottom: 20px;

    flex-wrap: wrap;
}

